h1, h2, h3 {
  color: $lightblue-color;
}

.button {
  text-transform: uppercase;
  padding: 15px 40px;
  border: 1px solid transparent;
  border-radius: 30px;
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  text-align: center;
  background: linear-gradient(146.13deg, #ffc341 20.46%, #ffd701 65.31%);
  color: $white-color;
}

.input {

}

.layout {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
}
